const MenSubMenu =(props)=>{
    return(
        <Menu
        id="simple-menu"
        anchorEl={props.anchorEl}
        keepMounted
        open={Boolean(props.anchorEl)}
        onMouseLeave= {props.handleClose}
        
      >
        <MenuItem onClick={props.handleClose} onMouseOut={props.handleClose} >item1</MenuItem>
        <MenuItem onClick={props.handleClose} onMouseOut={props.handleClose} >item2</MenuItem>
        <MenuItem onClick={props.handleClose} onMouseOut={props.handleClose} >item3</MenuItem>
        <MenuItem onClick={props.handleClose} onMouseOut={props.handleClose} >item4</MenuItem>
        <MenuItem onClick={props.handleClose} onMouseOut={props.handleClose} >item5</MenuItem>
        <MenuItem onClick={props.handleClose} onMouseOut={props.handleClose} >item6</MenuItem>
        <MenuItem onClick={props.handleClose} onMouseOut={props.handleClose} >item7</MenuItem>
        <MenuItem onClick={props.handleClose} onMouseOut={props.handleClose} >item8</MenuItem>
        <MenuItem onClick={props.handleClose} onMouseOut={props.handleClose} >item9</MenuItem>
      </Menu>
    )
}

export default MenSubMenu;