import {React,useState} from "react";
import "./all.css";

import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";
import Checkbox from '@material-ui/core/Checkbox';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormGroup from '@material-ui/core/FormGroup';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
    root: {
      display: 'flex',
    },
    formControl: {
      margin: theme.spacing(3),
    },
  }));

const SideBar = (props) => {
    const [value, setValue] = useState('female');

    const handleChange = (event) => {
      setValue(event.target.value);
    };


const classes = useStyles();
  const [state, setState] = useState({
    gilad: true,
    jason: false,
    antoine: false,
  });


  const handleCheckChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  const { gilad, jason, antoine } = state;

  return (
    <div class="sidenav">
      <FormControl component="fieldset">
        <FormLabel component="legend">FILTER</FormLabel>
        <RadioGroup
          aria-label="gender"
          name="gender1"
          value={value}
          onChange={handleChange}
        >
          <FormControlLabel value="MEN" control={<Radio />} label="MEN" />
          <FormControlLabel value="WOMEN" control={<Radio />} label="WOMEN" />
          <FormControlLabel value="BOYS" control={<Radio />} label="BOYS" />
          <FormControlLabel value="GIRLS" control={<Radio />} label="GIRLS" />
          
        </RadioGroup>
      </FormControl>
      <hr />
      <FormControl component="fieldset" className={classes.formControl}>
        <FormLabel component="legend">Categories</FormLabel>
        <FormGroup>
          <FormControlLabel
            control={<Checkbox checked={gilad} onChange={handleCheckChange} name="gilad" />}
            label="Shirts"
          />
          <FormControlLabel
            control={<Checkbox checked={jason} onChange={handleCheckChange} name="jason" />}
            label="Sleep Shirts"
          />
          <FormControlLabel
            control={<Checkbox checked={antoine} onChange={handleCheckChange} name="antoine" />}
            label="Dog Shirts"
          />
        </FormGroup>
        
      </FormControl>
      <hr />
      <FormControl component="fieldset" className={classes.formControl}>
        <FormLabel component="legend">Categories</FormLabel>
        <FormGroup>
          <FormControlLabel
            control={<Checkbox />}
            label="Brand1"
          />
          <FormControlLabel
            control={<Checkbox />}
            label="Brand2"
          />
          <FormControlLabel
            control={<Checkbox />}
            label="Brand3"
          />
          <FormControlLabel
            control={<Checkbox />}
            label="Brand4"
          />
          <FormControlLabel
            control={<Checkbox />}
            label="Brand5"
          />
        </FormGroup>
        <FormHelperText>+999 more</FormHelperText>
      </FormControl>
      <hr />
    </div>
  );
};

export default SideBar;
